import Vue from 'vue'
import Router from 'vue-router'
import GithubTrending from '@/components/GithubTrending'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'GithubTrending',
      component: GithubTrending
    }
  ]
})



